import React, { Component } from 'react';
import { Row, Col } from 'react-bootstrap';
import { Link } from 'react-router-dom';
import { connect } from 'react-redux';
import { hideDuplicateFilter } from '../../../../../Services/Actions/assetsActions'

//filterBody: state.assets.filterBody,


class DuplicatePostAlert extends Component {
    onHideDuplicate = event => this.props.hideDuplicateFilter()
    render() {
        const { count, isCollection, collectionId, assets } = this.props
        return count && parseInt(count) > 0 ? <Row>
            <Col>
                <div className="d-flex alert alert-danger py-3">
                    <div className="d-flex flex-column flex-stack flex-grow-1">
                        <p className="mb-0 text-gray-800 text-center fs-7">{count} Duplicate images detected. To clear them out click on {assets.filterBody.hideDuplicate ? <Link to={`/rule/add?duplicate=true${isCollection ? `&collectionId=${collectionId}` : ``}`} className='fw-bold text-link text-danger text-decoration-none'>'Action'</Link> : <span className='fw-bold text-link text-danger text-decoration-none cursor-pointer' onClick={this.onHideDuplicate}>'Review'</span>}  </p>
                    </div>
                </div>
            </Col>
        </Row> : null
    }
}

const mapStateToProps = state => {
    return {
        assets: state.assets
    }
}
const mapDispatchToProps = dispatch => {
    return {
        hideDuplicateFilter: () => dispatch(hideDuplicateFilter())
    }
}
export default connect(mapStateToProps, mapDispatchToProps)(DuplicatePostAlert);